a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

h1, h2, h3, h4, p, ul {
  margin: 0;
  padding: 0;
}

button {
  border: none;
}

img {
  width: 100%;
  max-width: 100%;
  display: block;
}

.container {
  max-width: 480px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 15px;
}

@media screen and (width >= 768px) {
  .container {
    width: 768px;
    max-width: none;
  }
}

@media screen and (width >= 1200px) {
  .container {
    width: 1200px;
    max-width: none;
  }
}

body {
  font-family: Noto Serif, serif;
}

.header {
  background-color: #101820;
  height: 110px;
}

.header__container {
  justify-content: space-around;
  align-items: center;
  height: 100%;
  display: flex;
}

.header__image {
  width: auto;
  height: 100px;
}

.header__list {
  justify-content: center;
  align-items: center;
  gap: 50px;
  display: flex;
}

.header__link {
  color: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 32px;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.header__link:after {
  content: "";
  opacity: 0;
  background-color: #da291c;
  border-radius: 2px;
  width: 110%;
  height: 4px;
  margin-top: 2px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.header__link:hover {
  color: #999;
}

.header__item:first-child .header__link:after, .header__item:nth-child(2):hover .header__link:after, .header__item:last-child:hover .header__link:after {
  opacity: 1;
}

.header__button {
  color: #fff;
  cursor: pointer;
  background-color: #da291c;
  border-radius: 15px;
  width: 210px;
  height: 45px;
  font-size: 22px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.header__button:hover {
  background-color: #aa2319;
}

.hero {
  background-image: url("hero-bg.a01debc2.png");
  background-position: center;
  background-size: cover;
  height: 600px;
}

.hero__container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  height: 100%;
  display: flex;
}

.hero__title {
  color: #fff;
  text-align: center;
  font-size: 118px;
}

.hero__describtion {
  color: #fff;
  margin-top: 20px;
  font-size: 32px;
}

.details {
  background-color: #da291c;
  padding-top: 40px;
  padding-bottom: 40px;
}

.details__title {
  text-align: center;
  color: #101820;
  margin-bottom: 40px;
  font-size: 48px;
}

.details__item {
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.details__text {
  color: #fff;
  max-width: 650px;
  font-size: 24px;
}

.details__image {
  background-color: #fff;
  width: 250px;
  height: 250px;
}

.style {
  background-color: #101820;
  justify-content: center;
  padding-top: 40px;
  padding-bottom: 40px;
  display: flex;
}

.style__image {
  width: 1000px;
}
